.bottom-box {
  position: absolute;
  bottom: 0;
  padding: 10px;
}
@-webkit-keyframes slowUp {
  0% {
    bottom: -100px;
  }
  100% {
    bottom: 0px;
  }
}
@keyframes slowUp {
  0% {
    bottom: -100px;
  }
  100% {
    bottom: 0px;
  }
}
@-webkit-keyframes slowRight {
  0% {
    width: 0px;
  }
  100% {
    width: 100%;
  }
}
@keyframes slowRight {
  0% {
    width: 0px;
  }
  100% {
    width: 100%;
  }
}
.ant-input-group .ant-input-affix-wrapper:not(:last-child) {
  border-top-left-radius: 11px;
  border-bottom-left-radius: 11px;
}
.edit-box {
  position: relative;
  -webkit-animation: slowUp 0.5s ease-out;
          animation: slowUp 0.5s ease-out;
  height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.edit-box .edit-icon {
  position: absolute;
  left: 0;
  top: -10px;
  width: 80px;
  height: 80px;
  background: url(/static/media/edit_yellow.faaf8b9f.png) no-repeat top;
  background-size: contain;
  cursor: pointer;
}
.edit-box .exit-active {
  padding: 10px 30px 10px 100px;
  bottom: 0;
  background: #ffffff;
  box-shadow: 0 3px 10px 0 rgba(190, 190, 190, 0.5);
  border-radius: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 24px;
  -webkit-animation: slowRight 1s ease-in-out;
          animation: slowRight 1s ease-in-out;
  cursor: pointer;
}
.edit-box .exit-active .box {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: lightgray;
  padding: 5px 10px;
  border: 1px solid black;
}
.edit-box .exit-active span {
  margin-right: 10px;
  color: black;
  font-size: 20px;
  line-height: 20px;
}
.edit-box .exit-active .exit-icon {
  width: 24px;
  height: 24px;
  background: url(data:image/png;base64,) no-repeat top;
  background-size: contain;
  cursor: pointer;
}
@-webkit-keyframes upandrigtht {
  0% {
    margin-bottom: -24px;
  }
  100% {
    margin-bottom: 24px;
  }
}
@keyframes upandrigtht {
  0% {
    margin-bottom: -24px;
  }
  100% {
    margin-bottom: 24px;
  }
}
.bottom-box .voice-active {
  bottom: 0;
  background: #ffffff;
  box-shadow: 0 3px 10px 0 rgba(190, 190, 190, 0.5);
  border-radius: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 45px;
  line-height: 45px;
  margin-bottom: 24px;
  -webkit-animation: upandrigtht 0.5s ease-out;
          animation: upandrigtht 0.5s ease-out;
}
.bottom-box .voice-active .voice-icon {
  width: 80px;
  height: 80px;
  background: url(/static/media/voice_yellow.a90e083e.png) no-repeat top;
  background-size: contain;
  cursor: pointer;
}
.bottom-box .voice-active .gif {
  position: absolute;
  left: 80px;
  width: 100px;
  height: 33px;
  background: url(/static/media/voiceGif.24207d98.gif) no-repeat center;
  background-size: cover;
  margin-left: 16px;
}
.bottom-box .voice-active span {
  height: 45px;
  line-height: 45px;
  text-align: center;
  font-size: 18px;
  color: #000000;
  -webkit-animation: slowRight 1s ease-in-out;
          animation: slowRight 1s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.bottom-box .tool-bar:hover {
  opacity: 1;
}
.bottom-box .tool-bar {
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 3px 10px 0 rgba(196, 196, 196, 0.5);
  border-radius: 4.5px;
  display: flex;
  flex-direction: row;
  padding-right: 14px;
  align-items: center;
  opacity: 0.2;
}
.bottom-box .tool-bar button {
  border: none !important;
  padding: 5px;
  margin-left: 14px;
  width: 23px;
  height: 28px;
  cursor: pointer;
  margin-top: 5px;
  margin-bottom: 5px;
}
.bottom-box .tool-bar .upload {
  background: url(/static/media/toUpload.fe798564.png) no-repeat center;
  background-size: contain;
}
.bottom-box .tool-bar .voice {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.bottom-box .tool-bar .voice:hover {
  background: url(/static/media/icon_voice_yellow.b806497e.png) no-repeat center;
  background-size: contain;
}
.bottom-box .tool-bar .input {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.bottom-box .tool-bar .input:hover {
  background: url(/static/media/icon_input_yellow.26b48ad9.png) no-repeat center;
  background-size: contain;
}
.bottom-box .tool-bar .edit {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.bottom-box .tool-bar .edit:hover {
  background: url(/static/media/icon_edit_yellow.652e118e.png) no-repeat center;
  background-size: contain;
}
.bottom-box .tool-bar .toDatabase {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}
.bottom-box .tool-bar .toDatabase:hover {
  background: url(/static/media/icon_database_yellow.89b74514.png) no-repeat center;
  background-size: contain;
}
.bottom-box .tool-bar .toVis {
  background: url(/static/media/toVis.996e344b.png) no-repeat center;
  background-size: contain;
  margin-right: 14px;
}
.ant-notification-center {
  left: 50%;
  bottom: 50% !important;
  margin-right: 30%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
